import styled from 'styled-components';

export const CommentDiv = styled.div`
    border-top: 1px solid #e5ebe4;
    margin-top: 30px;   
    width:100%;
    height:auto;
`;

export const CommentUser = styled.a`
    display: inline-block;
    margin: 10px 10px;
    :link { 
        text-decoration:none;
    }
    :visited {
        text-decoration:none;
    }
`;

export const Content = styled.span`
    display: block;
    width: 80%;
    margin: 12px;
    line-height: 1.3em;
`;
export const PageUp = styled.a`
    margin:15px;
    margin-left: 400px;
    :link { 
        text-decoration:none;
    }
    :visited {
        text-decoration:none;
    }
`;
export const PageDown = styled.a`
    margin:15px;
    margin-left: 10px;
    :link { 
        text-decoration:none;
    }
    :visited {
        text-decoration:none;
    }

`;