import React, { Component } from 'react';
import {
    Wrap,
    Item
} from './style'
import Login from "./login";

export default class Header extends Component {
    render() {
        return(
            <div>
                <Wrap>
                    <Item href={"/"} style={{color:"black", marginLeft:"50px", fontSize:"200%", fontWeight:"bolder"}}>
                        MOVIE
                    </Item>
                    <Item href={"#"} style={{ marginLeft: "100px"}}>电视</Item>
                    <Item href={"#"}>电影</Item>
                    <Item href={"#"}>原创</Item>
                    <Item href={"#"}>儿童</Item>
                    <Item href={"#"}>网络</Item>
                    <div style={{width:"60%"}}></div>
                    <Item href={"#"} style={{ order:"99"}}>
                        <Login />
                    </Item>

                </Wrap>
                <div style={{height:"90px"}}></div>
            </div>
            
        )
    }
}