import React, { Component } from 'react';
import axios from "axios"
import MovieItem from './component/movieItem';
// import {
//   CHANGE_SLIDER_LEFT,
//   CHANGE_SLIDER_RIGHT,
//   AXIOS_JSON_MOVIES
// } from './actionType';
import {
  Display,
  Outer,
  PageBtnRight,
  PageBtnLeft,
} from './style'
// import store from '../../../store'

class Slider extends Component {
  constructor(props) {
    super(props);
    //this.state = store.getState().slider;

    //注重复使用的组件 它自己的私有数据 
    this.state = {
      totPage:3,
      curPage:1,
      movieList:[]
    }
    //this.handleAxiosJson = this.handleAxiosJson.bind(this);
    //this.handleStoreChange = this.handleStoreChange.bind(this);
    this.handleClickLeft = this.handleClickLeft.bind(this);
    this.handleClickRight = this.handleClickRight.bind(this);
    //store.subscribe(this.handleStoreChange);
  }

  componentWillMount() {
    axios.get(this.props.api).then((res) => {
     const data = res.data.data.movies;
     console.log(this.state)
     this.setState({movieList:data})
     //this.handleAxiosJson(data);
    }).catch(()=>{
      console.log("fail");
    })
  }

  // handleAxiosJson(data) {
  //   const action = {
  //     type: AXIOS_JSON_MOVIES,
  //     data
  //   }
  //   store.dispatch(action);
  // }

  handleClickRight() {
    if(this.state.curPage !== this.state.totPage) {
      this.setState({curPage:this.state.curPage+1})
    }
    // const action = {
    //   type: CHANGE_SLIDER_RIGHT,
    //   page: this.state.curPage+1
    // }
    // store.dispatch(action);
  }

  handleClickLeft() {
    if(this.state.curPage !== 1) {
      this.setState({curPage:this.state.curPage-1})
    }
    // const action = {
    //   type: CHANGE_SLIDER_LEFT,
    //   page: this.state.curPage-1
    // }
    // store.dispatch(action);
  }

  // handleStoreChange() {
  //   this.setState(store.getState().slider); //这里变量名写错会导致无法更新到当前的state
  // }

  render() {
    return (
      <div>
        <div style={{ width:"1418px" }}>
        <PageBtnLeft style={{visibility:(this.state.curPage===1)?'hidden':'visible'}} onClick={this.handleClickLeft} />
          <Display>
            <Outer style={{ left: String((this.state.curPage - 1) * -1430) + 'px' }}>
                <MovieItem movieList={this.state.movieList}  />
            </Outer>
          </Display>
          <PageBtnRight style={{visibility:(this.state.curPage===this.state.totPage)?'hidden':'visible'}} onClick={this.handleClickRight} />
        </div>
      </div>
    )
  }
}

export default Slider;