import styled from 'styled-components';

export const Display = styled.div`
    width: 1420px;
    height: 309px;
    margin-left: 50px;
    overflow: hidden;
` 

export const Outer = styled.div`
    width: 4290px;
    height: 309px;
    position: relative;
    display:flex;
    transition: all 1s ease-out ;
`
export const Element = styled.div`
    width: 1430px;
    height: 309px;
    display:flex;
    flex-grow:1
`

export const PageBtnLeft = styled.img.attrs({
    src:'../../../static/img/up.png'
})`
    position: relative;
    float:left;
    top:118px;
    width: 50px;
    height: auto;
    border-radius:50%;
    border: 1px;
    border-color:black;
`;
export const PageBtnRight = styled.img.attrs({
    src:'../../../static/img/down.png'
})`
    position: relative;
    float:right;
    top:-191px;
    left: 100px;
    width: 50px;
    height: auto;
    border-radius:50%;
    border: 1px;
    border-color:black;
`;

