import React, { Component, Fragment } from 'react';
import axios from "axios";
import {
    CommentDiv,
    CommentUser,
    Content,
    PageUp,
    PageDown
} from "./style";

export default class Comment extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.showComment = this.showComment.bind(this);
        this.handleClicPage = this.handleClicPage.bind(this);
    }

    componentDidMount() {
        axios.get("/api/moive_1_comment_1.json").then((res)=>{
            this.setState(res.data);
        }).catch(()=> console.log("fail"));
    }
    showComment() {
        if(this.state.comment !== undefined) {
            return(
                <Fragment>
                    {this.state.comment.map( (item) => {
                         return(
                            <CommentDiv>
                                <CommentUser href={item.url}>{item.user}</CommentUser>
                                <span>{item.time}</span>
                                <Content>{item.comment}</Content>
                            </CommentDiv>
                         )
                        })
                    }
                </Fragment>
        )}
        else {
            return(
                <Fragment></Fragment>
            )
        } 
    }
    handleClicPage(pos,where) {
        //之后把这里改为 post 方法
        // const data = {
        //     pagecur:pos,  //pos类型为字符串
        //     handle:where  //where=0 pageup; where=1 pagedown
        // }
        axios.get("/api/moive_1_comment_2.json").then((res)=>{
            this.setState(res.data)
        }).catch(()=> console.log("fail"));
        console.log(pos,where)
    }    
    render() {
        return(
            <Fragment>
                {this.showComment()}
                <PageUp style={{color:this.state.pagecur==="1"?"black":"blue"}} onClick={this.state.pagecur==="1"?()=>{}:this.handleClicPage.bind(this,this.state.pagecur,0)} >上一页</PageUp>
                <PageDown style={{color:this.state.pagecur===this.state.pagetotal?"black":"blue"}} onClick={this.state.pagecur===this.state.pagetotal?()=>{}:this.handleClicPage.bind(this,this.state.pagecur,0)}>下一页</PageDown>
                

                <div style={{height:"200px", width:"100%"}}></div>

            </Fragment>
        )
    }
}