import React from 'react';
import Slider from './slider/slider';
import { Title } from './style';
export default function Home() {
    return(
        <div>
            <div style={{marginRight:"auto", marginLeft:"auto"}}>
                <Title >热门电影</Title>
                <Slider api='/api/home_movies.json'/>
            </div>
            <div>
                <Title >猜你喜欢</Title>
                <Slider api='/api/home_movies.json'/>
            </div>
            <div>
                <Title >电影推荐</Title>
                <Slider api='/api/home_movies.json'/>
            </div>
        </div>
       
    )
}