import styled from 'styled-components';

export const MovieDiv = styled.div`
    width: 293px;
    height: 309px;
    flex-grow:1;
    text-align:center
`;
export const MovieImg = styled.img`
    height: 260px;
    width: auto;
`;

export const MovieTitle = styled.a`
    display: block;
    margin-top: 6px;
    font-size: 17px;
    font-family: Helvetica, Arial, sans-serif;
    color: #333333;
    :link { 
        text-decoration:none;
        color: #333333;
    }
    :visited {
        text-decoration:none;
        color: #333333;
    }
`;