import React, { Component, Fragment } from 'react';
import axios from "axios";
import Comment from "./comment";
import {
  Display,
  MovieImg,
  MovieLab,
  CommentDiv,
  MovieDiv,
  CommentClass,
} from './style';

export default class Detail extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.handleJsonObj = this.handleJsonObj.bind(this);
  }
  componentDidMount() {
    axios.get("/api/moive_1.json").then((res) => {
      const data = res.data;
      this.setState(data);
      //this.handleAxiosJson(data);
    }).catch(() => {
      console.log("fail");
    })
  }
  handleJsonObj() {
    const data = this.state.message;
    const a = []
    for (let i in data) {
      a.push(<MovieLab key={i}>{i}: {data[i]}</MovieLab>)
    }
    return a
  }
  render() {
    if (this.state.data === undefined) {
      return (
        <div></div>
      )
    }
    else {
      return (
          <Display>
            <MovieDiv>
              <div style={{ marginBottom: "20px" }}>
                <span style={{ fontWeight: "550", fontSize: "26px", color: "#494949" }}>{this.state.data.title}</span>
                <span style={{ fontSize: "26px", color: "#888888" }}>({this.state.data.year})</span>
              </div>
              <MovieImg src={this.state.data.img} />
              {this.handleJsonObj()}
              <div style={{ weight: "100%", height: "100px", marginTop: "80px" }}>
                <span style={{ fontWeight: "550", marginBottom: "22px", display: "block", fontSize: "23px", color: "#000000" }}>{this.state.data.title}剧情介绍</span>
                <span style={{ lineHeight: "1.5em", fontSize: "18px", color: "#4B4A4A" }}>{this.state.剧情简介}</span>
              </div>
            </MovieDiv>
            <CommentDiv>
              <span style={{ fontWeight: "550", marginBottom: "22px", display: "block", fontSize: "23px", color: "#000000" }}>{this.state.data.title}短评</span>
              <CommentClass>
                <Comment></Comment>
              </CommentClass>
            </CommentDiv>
          </Display>
      )
    }

  }
}