import React, {Fragment} from 'react';
import {
    MovieDiv,
    MovieImg,
    MovieTitle
} from './style'

//注意，利用props在父子组件中传递时，不需要写 props={。。。} 因为默认传递的值为子组件 props 的一个属性

export default function MovieItem(props) {
    if(props.movieList[0] !== undefined)
    {
        return(
            <Fragment>
                {props.movieList.map((item)=>{
                    console.log(item.id)
                    return(
                    <MovieDiv key={item.id}>
                        <a href={item.url} ><MovieImg src={item.img} /></a>
                        <MovieTitle href={item.url}>{item.title}</MovieTitle>
                    </MovieDiv>)
                })}
            </Fragment>
            )
    }
    else {
        return(
            <div></div>
        )
    }
}