import React from 'react';
import Modal from 'react-modal';
import { Close, Lbutton, Linput } from "./style";
const customStyles = {
  content : {
    top                   : '40%',
    left                  : '50%',
    width                 : "20%",
    height                : "auto",
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)

export default class Login extends React.Component {
  constructor() {
    super();

    this.state = {
      modalIsOpen: false
    };

    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  openModal() {
    this.setState({modalIsOpen: true});
  }

  afterOpenModal() {
    // references are now sync'd and can be accessed.
    this.subtitle.style.color = '#00';
  }

  closeModal() {
    this.setState({modalIsOpen: false});
  }

  render() {
    return (
      <div>
        <span onClick={this.openModal}>Login</span>
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          style={customStyles}
          contentLabel="Example Modal"
        >
          <Close onClick={this.closeModal}></Close>
          <h2 ref={subtitle => this.subtitle = subtitle}>Login</h2>
          <br/>
          <form>
          <label for="username" styled={{fontWeight: "800"}}>用户名：</label>
          <Linput style={{position: "relative", marginLeft: "10px"}} className="username" />
          <br/>
          <br/>
          <label for="passwd" styled={{fontWeight: "800"}}>密码:</label>
          <Linput style={{position: "relative", marginLeft: "38px", marginBottom: "15px"}} className="passwd" />
          <br />
          <Lbutton type="submit">登陆</Lbutton>
          </form>
        </Modal>
      </div>
    );
  }
}
